body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes marquee {
  0%   { -webkit-transform: translate(0, 0); transform: translate(0, 0); }
  100% { -webkit-transform: translate(-100%, 0); transform: translate(-100%, 0); }
}

@keyframes marquee {
  0%   { -webkit-transform: translate(0, 0); transform: translate(0, 0); }
  100% { -webkit-transform: translate(-100%, 0); transform: translate(-100%, 0); }
}
.main-page {
  text-align: center;
}

.main-page-logo {
  -webkit-animation: spin infinite 20s linear;
          animation: spin infinite 20s linear;
  height: 40vmin;
}

.main-page-header {
  /* background-color: #282c34; */
  background-color: #1d1924;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;
}

.main-page-link {
  color: #61dafb;
}



.audio-player-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
}

.component-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}







input[type="range"].range { 
  cursor: pointer;
  height: 9.5px;
  -webkit-appearance: none; 
  z-index: 200; 
  border: 1px solid #b3b3b3; 
  background-color: #b3b3b3; 
  background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#b3b3b3), to(white)); 
  background-image: -webkit-linear-gradient(right, #b3b3b3, white); 
  background-image: -o-linear-gradient(right, #b3b3b3, white); 
}

input[type="range"].range:focus {
  border: 0 !important;
  outline: none !important; 
}

input[type="range"].range::-webkit-slider-thumb {
  -webkit-appearance: none; 
  width: 10px; 
  height: 10px; 
  background-color: #815fdd; 
  background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#8a6fd6), to(#815fdd)); 
  background-image: -webkit-linear-gradient(right, #8a6fd6, #815fdd); 
  background-image: -o-linear-gradient(right, #8a6fd6, #815fdd); 
}

input[type="range"].round { 
  border-radius: 20px; 
}

input[type="range"].round::-webkit-slider-thumb { 
  border-radius: 5px;
}

.vertical-highest-first {
  -webkit-transform:rotate(270deg); 
  transform:rotate(270deg); 
}
.audio-player-container figure {
  margin-left: 0;
}

.audio-player-container img {
  border-radius: 50%;
  height: 100px;
}

.album-cover img {
  -webkit-animation: spin infinite 20s linear;
          animation: spin infinite 20s linear;
}
.marquee {
  width: 210px;
  align-self: flex-start;
  overflow: hidden;
  white-space: nowrap;
}

.marquee p {
  display: inline-block;
  padding-left: 100%;
  text-indent: 0;
  -webkit-animation: marquee 10s linear infinite;
          animation: marquee 10s linear infinite;
  margin-top: 0;
}

.marquee img {
  position: relative;
  top: 5px;
  margin-right: 5px;
}


.audio-player-controls {
  display: flex;
  align-items: center;
  position: relative;
}

.audio-player-controls button {
  height: 100%;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #b3b3b3;
  outline: none;
}

.audio-player-controls button i {
  font-size: 25px;
}
.audio-player-controls .fa-play-circle,
.audio-player-controls .fa-pause-circle {
  font-size: 40px;
}

.audio-player-controls .fa-random {
  font-size: 20px;
  margin-left: 20px;
}

.audio-player-controls .fa-redo-alt {
  font-size: 17px;
}

.audio-player-controls button:hover {
  color: white;
}

.audio-player-controls .active {
  color: white;
}

.volume-wrapper:hover button {
  color:white;
}
.seek-slider-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.seek-slider-wrapper p {
  margin-left: 10px;
}

.seek-slider {
  margin-right: 10px;
  width: 120px;
}


.volume-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 20px;
}

.volume-button {
  font-size: 25px;
}

.volume-wrapper input[type="range"] {
  position: absolute;
  width: 60px;
  bottom: 60px;
  -webkit-transition: 0.2s 0.5s;
  transition: 0.2s 0.5s;
}

.volume-slider {
  visibility: hidden;
}

.volume-wrapper:hover .volume-slider {
  visibility: visible;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}
